<template>
  <div id="edit-note">
    <head-top title="编辑笔记">
      <span slot="right" class="head-right" @click="saveData">
        <i class="iconfont icon-finish"></i>
      </span>
    </head-top>

    <div class="main">
      <div class="tt">
        <input type="text" placeholder="编辑笔记标题" v-model="title" maxlength="50"/>
      </div>
      <r-quill-editor :funType="funType" @change="editorChange"
                      max-height="calc(60vh)"></r-quill-editor>
    </div>
  </div>
</template>

<script>
import RQuillEditor from "@/components/r-quill-editor";

import { ModulePicture } from "@/utils/enum";
import { emojValidator } from "@/utils/index";

export default {
  name: "",
  components: { RQuillEditor },
  props: {},
  data () {
    return {
      title: "",
      content: "",
      imgBase: "",
      funType: ModulePicture.StudentNote,
      saveLoading: false
    };
  },
  methods: {
    saveData () {
      if (this.saveLoading) return;
      this.saveLoading = true;
      if (!this.title) {
        this.$toast("笔记标题不能为空");
        this.saveLoading = false;
        return;
      } else if (emojValidator(this.title)) {
        this.$toast("不支持表情字符");
        this.saveLoading = false;
        return;
      }

      if (!this.content) {
        this.$toast("笔记内容不能为空");
        this.saveLoading = false;
        return;
      } else if (emojValidator(this.content)) {
        this.$toast("不支持表情字符");
        this.saveLoading = false;
        return;
      }

      this.$mainHttp.note.postSave({
        snID: "",
        ciID: this.$store.getters.courseID,
        chID: this.$store.getters.course["CHID"],
        studentID: this.$store.getters.studentID,
        title: this.title,
        content: this.content
      }).then(res => {
        if (res.Result) {
          this.$toast.success("添加笔记成功");
          this.$router.push("/note");
        } else if (res.Info) {
          this.$toast(res.Info);
        }
        this.saveLoading = false;
      }).catch(() => {
        this.$toast("添加失败，请重试");
        this.saveLoading = false;
      });
    },

    editorChange (value) {
      this.content = value;
    }
  }
};
</script>

<style lang='less' scoped>
@import url("./../../../styles/common/base.less");

#edit-note {
  width: 100%;
  height: 100%;
  background: #ebf0ef;

  .main {
    text-align: left;
    background: #ebf0ef;

    .tt {
      width: 92vw;
      padding-left: 4vw;
      padding-right: 4vw;
      color: #999;
      height: 40px;
      line-height: 40px;
      background: #fff;

      input {
        color: #999;
        height: 35px;
        line-height: 35px;
        width: 100%;
      }
    }
  }
}
</style>



